<template>
  <div class="page-approval-detail">
    <!-- <van-nav-bar title="" @click-left="onClickNavBarLeft" left-arrow :fixed="true" /> -->
    <div class="main-content">
      <van-cell-group title="">
        <van-cell title="">{{ illegalInfoDetail.offenderName }}</van-cell>
        <van-cell title="">{{ illegalInfoDetail.documentType }}</van-cell>
        <van-cell title="">{{ illegalInfoDetail.documentNumber }}</van-cell>
      </van-cell-group>
      <van-cell-group title="">
        <van-cell title="">{{ illegalInfoDetail.violationType }}</van-cell>
        <van-cell title=""
          >{{ illegalInfoDetail.carNumber }}
          <van-tag
            v-show="illegalInfoDetail.carNumber"
            style="margin-left:5px;"
            plain
            type="primary"
            @click="checkCarNum(illegalInfoDetail.carNumber)"
            ></van-tag
          ></van-cell
        >
      </van-cell-group>
      <van-cell-group title="">
        <van-cell title="">{{ illegalInfoDetail.violationTime | datetime }}</van-cell>
        <van-cell title="">{{ illegalInfoDetail.punishRoad }}</van-cell>
        <van-cell title="">{{ illegalInfoDetail.punishPlace }}</van-cell>
        <van-cell title="">{{ illegalInfoDetail.violationCode }}</van-cell>
        <van-cell title="">{{ illegalInfoDetail.violationAction }}</van-cell>
        <van-cell title="">{{ illegalInfoDetail.penaltyAmount }}</van-cell>
        <van-cell title="">{{ illegalInfoDetail.punishType }}</van-cell>
      </van-cell-group>
      <van-cell-group title="">
        <div class="image-show-wrapper" v-if="illegalImagesList.length > 0">
          <van-image
            v-for="(img, index) in illegalImagesList"
            :key="index"
            lazy-load
            class="image-show"
            :src="host + img.filePath"
            @click="imgPreview(index)"
          />
        </div>
        <div v-else class="image-show-wrapper">
          <span style="margin-left:5px;color:#323233"></span>
        </div>
      </van-cell-group>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import qs from 'querystring'
import { mapState, mapActions } from 'vuex'
import { Toast, CellGroup, Cell, Tag, Lazyload, Image, ImagePreview } from 'vant'
import request, { host } from '@/utils/request'

Vue.use(Toast)
  .use(CellGroup)
  .use(Cell)
  .use(Tag)
  .use(Lazyload)
  .use(Image)
  .use(ImagePreview)

export default {
  name: 'approval-detail',
  components: {},
  props: ['id'],
  data() {
    return {
      host: host,
      illegalInfoDetail: {},
      illegalImagesList: []
    }
  },
  created() {
    this.getPunishmentDetail()
    this.getPunishImageList()
  },
  mounted() {},
  methods: {
    imgPreview(index) {
      ImagePreview({
        images: this.illegalImagesList.map(item => host + item.filePath),
        startPosition: index,
        onClose() {
          // do something
        }
      })
    },
    async getPunishmentDetail() {
      const response = await request(`/api/tPunishment/getById/${this.id}`, {
        method: 'post'
      })
      if (response.resultCode === 200) {
        this.illegalInfoDetail = response.data
      }
    },
    async getPunishImageList() {
      const response = await request(`/api/tPunishImage/getPunishImageByPunishId/${this.id}`, {
        method: 'post'
      })
      if (response.resultCode === 200) {
        this.illegalImagesList = response.data
      }
    },
    // 
    checkCarNum(carNum) {
      this.$router.push({
        name: 'search',
        params: {
          carNum
        }
      })
    }
  }
}
</script>

<style lang="less">
@import '../style/var';
