  .image-show-wrapper {
    display: flex;
    justify-content: stretch;
    align-items: flex-start;
    padding: 10px;
    .image-show {
      width: 30%;
      max-width: 100vw;
    }
  }

  .van-cell {
    .van-cell__value {
      flex: 2;
      text-align: left;
      color: @black;
    }
  }
}
</style>
